html, body {
  margin: 0;
  padding: 0;
  overflow: hidden;
}

body{
  padding: 50px;
}

canvas {
  padding: 0;
  margin: 0;
  display: block;
  position: absolute;
  left: 0;
  top: 0;
}

#root {
  height: 100%;
}

#upload {
  position: absolute;
  bottom: 10px;
  right: 20px;
}

#tools {
  position: absolute;
  bottom: 10px;
  right: 20px;
}

.subtitles-container {
  padding-bottom: 50px;
  padding-left: 50px;
  padding-right: 50px;
  position:absolute;
  width: 1920px;
  height: 1080px;
  top: 0;
  left:0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.subtitles-text {
  font-family: -apple-system, BlinkMacSystemFont,"helvetica neue", helvetica, arial, sans-serif;
  font-size: 54px;
  line-height: 64px;
  color: #eee;
  background-color: rgba(0,0,0,0.33);
  padding: 5px 10px;
  text-shadow: 0 0 5px rgba(0,0,0,0.5);
  -ms-flex-item-align: end;
      align-self: flex-end;
  text-align: center;
  width: 100%;
}

#logo {
  background-image: url(https://left.gallery/images/logo.png);
  width: 150px;
  height: 100px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  position: absolute;
  top: 5px;
  right: 20px;
  margin: 20px;
}


#token-logo {
  background-image: url(tokenlogo.png);
  width: 100px;
  height: 100px;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  position: absolute;
  top: 10px;
  left: 30px;
  margin: 20px;
}



